import React, { use, useEffect } from "react";
import HeaderNav from "../components/common/HeaderNav";
import { useGlobalContext } from "../GlobalContext";
import CustomImage from "../components/ui/CustomImage";
import { useRouter } from "next/router";

export default function Page() {
  const { coinPrices } = useGlobalContext();
  const router = useRouter();
  // –î–æ–±–∞–≤–∏–ª–∏ router –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏

  return (
    <div className="wrapper !bg-[#1d1d1d]">
      <div className="bg-[#171717]">
        <HeaderNav />
      </div>
      <div className="flex items-center space-x-[10px] text-white p-[5px]">
        <div>
          <CustomImage
            width={20}
            height={20}
            alt="fire"
            src="/pages/SpotPage/fire.svg"
          ></CustomImage>{" "}
        </div>
        {coinPrices.slice(0, 8).map((coin, index) => (
          <div key={index}>
            <div>
              <p className="font-nekstmedium inline-block">
                {coin?.symbol}/USDT
              </p>
              <span
                className={`ml-[2px] text-[12px] font-nekstmedium ${
                  Number(coin?.price24hPcnt) >= 0
                    ? "text-green-500"
                    : "text-red-500"
                }`}
              >
                {Number(coin?.price24hPcnt)?.toFixed(2)}%
              </span>
            </div>
          </div>
        ))}
      </div>
      <div className="flex items-center rama">
        <div className="flex">{/* <CustomImage src='' /> */}</div>
      </div>
    </div>
  );
}
